import { Injectable, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class QuestionService implements OnInit {

  constructor(private http: HttpClient) { }

  ngOnInit() {
  }

  public getQuestions(): Observable<any> {
    return this.http.get('../assets/juniper2.json');
  }

  public formatAnswer(question) {
    return Object.keys(question.answers).map(key => {
      return { key, value: question.answers[key], checked: false, correct: this.detectCorrectAnswer(question, key) };
    });
  }

  private detectCorrectAnswer(question, qKey) {
    if (question.response.split(',').includes(qKey)) {
      return true;
    } else {
      return false;
    }
  }
}

