import { JuniCorrectionComponent } from './juni-correction/juni-correction.component';
import { JuniTestComponent } from './juni-test/juni-test.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  { path: 'test', component: JuniTestComponent },
  { path: 'correction', component: JuniCorrectionComponent },
  { path: '**', redirectTo: 'test' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
