import { Component, OnInit } from '@angular/core';
import { QuestionService } from '../services/question.service';

@Component({
  selector: 'app-juni-test',
  templateUrl: './juni-test.component.html',
  styleUrls: ['./juni-test.component.scss']
})
export class JuniTestComponent implements OnInit {
  questionsArr: [] = [];
  questions: [] = [];
  nextSection = 0;
  showAnswers = false;

  constructor(public questioService: QuestionService) { }

  ngOnInit() {
    this.questioService.getQuestions()
      .subscribe((allQuestions) => {
        allQuestions = allQuestions.map(q => {
          q.answers = this.questioService.formatAnswer(q);
          return q;
        });
        this.questionsArr = allQuestions;
        this.questions = allQuestions.slice(this.nextSection, this.nextSection + 60);
        this.nextSection += 60;
      });
  }

  nextQuestions(): void {
    if (this.nextSection === this.questionsArr.length) {
      return;
    }
    this.questions = [];
    this.questionsArr.slice(this.nextSection, this.nextSection + 60).forEach(x => this.questions.push(x));
    this.nextSection += 60;
  }

  show() {
    this.showAnswers = !this.showAnswers;
  }

  selectCorrectAnswer(ans) {
    const responses = ans.response.split(',');
  }

}
